import axios from 'axios'
export default{
    state:{
        // 
        list:[],  
        //  
        inputValue:'',
        // id
        nextId:5,
        // 
        viewkey:'all',
    },
    // mutationsstate
    mutations:{
        // list
        initList(state,list){
            state.list = list;
        },
        // storeinputvalue
        setInputValue(state,val){
            state.inputValue = val
        },
        // 
        addItem(state){
            const obj = {
                id:state.nextId,
                title:state.inputValue.trim(),
                done:false
            }
            state.list.push(obj);
            state.nextId ++;
            state.inputValue = '';
        },
        // 
        removeItem(state,id){
            state.list.forEach((item,index)=>{
                if(item.id === id){
                    state.list.splice(index,1)
                }
            })
        },
        // 
        changeStatus(state,paylaod){
            state.list.forEach((item,index)=>{
                if(item.id === paylaod.id){
                    state.list[index].done = paylaod.status
                }
            })
        },
        // 
        clear(state){
            state.list = state.list.filter(item=>item.done === false)
        },
        // 
        changeViewKey(state,key){
            state.viewkey = key
        },
    },
    actions:{
        // actions state
        // context.commit mutations
        // todo  json
        getList({commit}){
            axios.get("../../../static/list.json").then(({data})=>{
                commit('initList',data) 
                console.log(data)
            })
        },
    },
    getters:{
        // 
        unDoneLen(state){
            // filter
            // return state.list.filter(item=>item.done===false).length
            // let arr =  state.list.filter(item=>
            //     item.done === false
            // )
            let arr1 =  state.list.filter((item) => {
                return item.done === false
            })
            // console.log(arr,'arr')
            return arr1.length
      
        },
         // 
        changeListView(state){
            if(state.viewkey === 'all'){
                return state.list
            }else if(state.viewkey === 'undone'){
                return state.list.filter(item=>!item.done)
            }else if(state.viewkey === 'done'){
                return state.list.filter(item=>item.done)
            }
            return state.list
        },
    }
}




