export default{
    state:{
        count:0,
    },
    mutations:{
        // mutations
        add(state){
            console.log(222)
            state.count++
        },
        // mutations
        addN(state,step){
            state.count+=step
        },
        sub(state){
            state.count--
        },
        subN(state,step){
            state.count-=step
        },
    },
    actions:{
        // actions state
        // context.commit mutations
        addAsync(context){
            // context
            setTimeout(()=>{
                context.commit('add')  
            },1000)
        },
        // actions
        addNAsync(context,step){
            setTimeout(()=>{
            context.commit('addN',step)
            },1000)
        },
        subAsync({commit}){
            setTimeout(()=>{
                commit('sub')  
            },1000)
        },
        subNAsync({commit},step){
            setTimeout(()=>{
                commit('subN',step)  
            },1000)
        },
    },
    getters:{
        showNum(state){
            return ''+state.count+''
        },
    }
}




