<template>
  <div class="left">
    <!-- el-menu -->
    <!-- 
        router:  vue-router  index  path  false
        unique-opened:    false
        collapse-transition     true
        collapse   false   true
    -->
    <el-menu
      :default-active="$route.path"
      class="el-menu-vertical"
      @select="select"
      background-color="#545c64"
      text-color="rgba(255,255,255,.7)"
      active-text-color="#ffd04b"
      :router="true"
      :unique-opened="true"
      :collapse-transition="true"
      :collapse="isCollapse"
    >
      <!--  -->
      <!-- v-if="!item.hidden" hidden -->
      <template v-for="(item,index) in $store.getters.routers" v-if="!item.hidden">
        <!-- alone -->
        <el-submenu v-if="!item.alone && item.children.length>0" :key="index" :index="index+''">
          <template slot="title">
            <!-- <svg-icon v-if="item.meta&&item.icon" :icon-class="item.icon"></svg-icon> -->
            <i :class="item.icon"></i>
            <span slot="title">{{ item.name }}</span>
          </template>
          <!--  -->
          <menu-tree :menuData="item.children"></menu-tree>
        </el-submenu>
        <!--  -->
        <!--  -->
        <el-menu-item :index="item.path" :key="index" v-else>
          <i :class="item.icon"></i>
          <!-- <svg-icon v-if="item.icon" :icon-class="item.icon"></svg-icon> -->
          <span slot="title">{{ item.name}}</span>
        </el-menu-item>
      </template>
    </el-menu>
  </div>
</template>
<script>
import menuTree from "./childmenu";
import { mapGetters } from "vuex";
export default {
  components: {
    menuTree
  },
  computed: {
    ...mapGetters(["routers", "isCollapse"])
  },
  data() {
    return {
      key: ""
    };
  },
  created() {
    console.log(this.$route.path, "=======");
    // index meta
    let newKey = localStorage.getItem("key");
    console.log(newKey, "create");
    this.key = newKey;
    console.log(this.key, "this create");
  },
  mounted() {
    // console.log(this.routers, "getters.router");
    // console.log(this.$store.getters.routers,'getters')
  },
  methods: {
    select(key) {
      // console.log(key, keyPath);
      // localStorage.setItem("key", key);
      // let newKey = localStorage.getItem("key");
      // console.log(newKey, "newkey");
      // this.key = newKey;
      // console.log(this.key, "this.key");
      //   store
      let router = this.$store.getters.routers;
      let name = "";
      // 
      let navTitle = function(path, routerARR) {
        for (let i = 0; i < routerARR.length; i++) {
          if (routerARR[i].children.length > 0 || routerARR[i].path === path) {
            if (
              routerARR[i].path === path &&
              routerARR[i].children.length < 1
            ) {
              name = routerARR[i].name;
              break;
            }
            navTitle(path, routerARR[i].children);
          }
        }
        return name;
      };
      this.$store.dispatch("addTab", {
        title: navTitle(key, router),
        path: key
      });
    }
  }
};
</script>
<style lang="scss">
.left {
  height: 100%;
  background: #545c64;
}

// 
.el-menu-vertical:not(.el-menu--collapse) {
  width: 220px;
  height: 100%;
  overflow-y: scroll;
}
// 
#asideNav .el-menu .el-menu-item.is-active {
  background-color: #56a9ff !important;
}
.el-menu-item.is-active {
  background-color: #56a9ff !important;
}
.el-menu {
  overflow: inherit;
  border-right: none;
}
// 
.el-menu::-webkit-scrollbar {
  display: none;
}
</style>






