import Vue from 'vue'
import Router from 'vue-router'
// layout
import layout from '../components/layout/layout'
// 
import commerView from '../views/commerview'
// 
// routerrouter -> index.js
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
Vue.use(Router)
// 
export const defaultRouter = [
  { path: '/',
    redirect: '/index',
    hidden: true,  //hidden 
    children: []
  },{
    path: '/index',
    component: layout,
    // redirect: '/index',
    alone: true,  //true  
    name: '',
    icon:'el-icon-s-home',
    children: [{
      path: '/index',
      name: '',
      icon:'el-icon-s-home',
      component: () => import('@/views/home/index'),
      children: []
    }]
  },{
    path: '/login',
    component: () => import('@/views/login/login'),
    name:'',
    children: [],
    hidden: true
  },
  {
    path: '*',
    hidden: true,
    children: [],
    component: () => import('@/views/404'),
  },
  {
    path: '/',
    component: layout,
    icon:'el-icon-setting',
    name: '',
    children: [{
      path: '/pagerole',
      name: '',
      icon:'el-icon-setting',
      component: () => import('@/views/roletype/page'),
      children: []
    }, {
      path: '/btnrole',
      name: '',
      icon:'el-icon-eleme',
      component: () => import('@/views/roletype/btn'),
      children: []
    }]
  }
]
// 
export const addRouter = [{
    path: '/two',
    component: layout,
    alone: true,  //true  
    name: '',
    icon:'el-icon-eleme',
    meta: {
      role: ['superAdmin']
    },
    children: [{
      icon:'el-icon-eleme',
      path: '/two',
      name:'',
      children: [],
      component: () => import('@/views/home/select'),
    }]
  },
  {
    path: '/three',
    component: layout,
    alone: true,  //true  
    name: '',
    icon:'el-icon-edit-outline',
    meta: {
      role: ['superAdmin','admin']
    },
    children: [{
      path: '/three',
      name: '',
      icon:'el-icon-edit-outline',
      children: [],
      component: () => import('@/views/home/three'),
      
    }]
  },
  {
    path: '/five',
    component: layout,
    alone: true,  //true  
    name: '',
    icon:'el-icon-thumb',
    meta: {
      role: ['superAdmin']
    },
    children: [{
      path: '/five',
      name: '',
      icon:'el-icon-thumb',
      component: () => import('@/views/home/five'),
      children: [],
    }]
  },  
  {
    path: '/six',
    component: layout,
    alone: true,  //true  
    name: '',
    icon:'el-icon-thumb',
    meta: {
      role: ['superAdmin']
    },
    children: [{
      path: '/six',
      name: '',
      icon:'el-icon-thumb',
      component: () => import('@/views/home/six'),
      children: [],
    }]
  }, 
  {
    path: '/seven',
    component: layout,
    // alone: true,  //true  
    name: '',
    icon:'el-icon-thumb',
    children: [{
      path: '/seven',
      name: '',
      icon:'el-icon-thumb',
      component: () => import('@/views/home/two'),
      children: [],
    }]
  }, 
  {
    path: '/person',
    component: layout,
    // alone: true,  //true  
    name: '',
    icon:'el-icon-s-check',
    children: [{
      path: '/person',
      name: '',
      icon:'el-icon-s-custom',
      component: () => import('@/views/home/four'),
      children: []
    },{
      path: '/personanal',
      name: '',
      icon:'el-icon-user',
      component: commerView,//
      children: [
        {
          path: '/sanji',
          name: '11',
          icon:'el-icon-sunny',
          component: () => import('@/views/news/news'),
          children: []
        },
        {
          path: '/sanjier',
          name: '22',
          icon:'el-icon-heavy-rain',
          component: () => import('@/views/person/person'),
          children: []
        }
      ],
    }]
  }, 
  {
    path: '*',
    hidden: true,
    component: () => import('@/views/404'),
    children: []
  },
]
// console.log(defaultRouter, 'routers')

export default new Router({
  routes: defaultRouter
})



